body {
  background-color: #ffffff;
  font-family: arial;
  font-size: 16px;
  text-align: center;
  padding: 1rem;
}

.info__image {
  display: inline-block;
  max-width: 30vw;
}

select {
  width: 10vw;
  font-size: 1rem;
}

input {
  width: 30vw;
  padding: 0.5rem;
  font-size: 1rem;
}

.steps {
  margin: 3rem 0;
}

label {
  display: block;
  margin: 0.5rem;
  font-weight: bold;
}

.container {
  display: block;
  margin: 1rem;
}



